/* src/styles.css */

/* Базовые стили для всего приложения */
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, 'Helvetica Neue', Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  background-color: var(--vkui--color_background_content);
}

/* Контейнер для центрирования контента */
.container {
  max-width: 600px;
  margin: 0 auto;
  padding: 20px;
  text-align: center;
}

/* Анимация для текста "Перенаправляем..." */
.redirect-text {
  animation: pulse 1.5s infinite;
  color: var(--vkui--color_text_primary);
}

@keyframes pulse {
  0% { opacity: 0.8; }
  50% { opacity: 0.4; }
  100% { opacity: 0.8; }
}

/* Стили для кнопки/ссылки (если нужно) */
.vkuiLink {
  color: var(--vkui--color_text_accent);
  text-decoration: none;
  transition: opacity 0.2s ease;
}

.vkuiLink:hover {
  opacity: 0.8;
}

/* Кастомные стили для VKUI-компонентов (опционально) */
.vkuiTitle--level-1 {
  margin-bottom: 24px !important;
}

/* Адаптация под мобильные устройства */
@media (max-width: 768px) {
  .container {
    padding-top: 30vh; /* Сдвигаем контент выше на мобильных */
  }
}